  // import { mapGetters } from 'vuex'
  export default {
    data () {
      return {
        formName: 'kehu_form',
        kehu_form: {},
        goodsNumber: false,
        isSelect: false
      }
    },
    created () {
    },
    computed: {
      goods_table () {
        return this.$store.getters.getFormContent(this.formName)
      }
    },
    mounted () {
    },
    methods: {
      add_goods () {
        this.kehu_form = this.$options.data().kehu_form
        layer.open({
          type: 1,
          title: ['客户编码添加', 'color:#000;background-color:#cae1f7;border-bottom:solid 1px #cae1f7;'],
          area: ['575px'],
          content: $('#add-customer-content'),   //内容
          resize: false,   //不允许拉伸
          shade: [0.1, '#3b6ea5'],   //遮罩阴影
          cancel: function () {
            document.getElementById('add-customer-content').style.display = 'none'
          }
        })
      },
      add_kehu_click () {
        let data = {}
        data.formId = this.formName
        data.content = this.kehu_form
        this.$store.commit('addFormInfo', data)
        layer.closeAll()
      },
      cancel_click () {
        layer.closeAll()
      },
      select_goods (i) {
        this.goodsNumber = i
      },
      change_goods () {
        if (!this.goodsNumber && this.goodsNumber != 0) {
          layer.msg('选中一个才能修改哦')
        } else {
          this.kehu_form = { ...this.goods_table[this.goodsNumber] }
          layer.open({
            type: 1,
            title: ['客户编码修改', 'color:#000;background-color:#cae1f7;border-bottom:solid 1px #cae1f7;'],
            area: ['575px'],
